import axios from 'axios'
import qs from 'qs'
import {Message} from 'element-ui'

// let baseURL = process.env.NODE_ENV == 'development'
//   ?
//   ($URL.JSON ? process.env.BASE_API : $URL.DATA_API)
//   :
//   ($URL.JSON ? $URL.BASE_API : $URL.DATA_API);
let baseURL =$URL.JSON ? './':$URL.DATA_API;
  
  console.log('baseURL:'+baseURL);
const service = axios.create({
  baseURL: baseURL, // api base_url
  // timeout: 6000 // request timeout
})

// console.log("" + $URL.BASE_API);
console.log("" + baseURL);

service.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';

//http request 
service.interceptors.request.use(
  config => {
    // 
    /**
     * var a = {name:'hehe',age:10};
     * qs.stringify(a)
     * => 'name=hehe&age=10'
     */

    // Vue.$loading.show();

    if (config.method == "post") {
      config.data = qs.stringify(config.data);
    }

    return config;
  },
  err => {
    return Promise.reject(err);
  }
);

//http response 
service.interceptors.response.use(
  res => {
    // Vue.$loading.hide();
    return res;
  },
  err => {
    // Vue.$loading.hide();
    if (err && err.response) {
      switch (err.response.status) {
        case 400:
          err.message = ''
          break;
        case 401:
          err.message = ''
          break;
        case 403:
          err.message = '403'
          break;
        case 404:
          err.message = '404,'
          break;
        case 405:
          err.message = ''
          break;
        case 408:
          err.message = ''
          break;
        case 500:
          err.message = ''
          break;
        case 501:
          err.message = ''
          break;
        case 502:
          err.message = ''
          break;
        case 503:
          err.message = '503'
          break;
        case 504:
          err.message = ''
          break;
        case 505:
          err.message = 'http'
          break;
        default:
          err.message = `${err.response.status}`
      }
    } else {
      err.message = ""
    }
    console.log('err' + err) // for debug
    Message({
      message: err.message,
      type: 'error',
      duration: 5 * 1000
    })
    return Promise.reject(err)
  }
)

export default service
